	module.exports = {"data":{"allDataJson":{"edges":[{"node":{"bmjsiteHead":[{"id":"34866726","accountId":"5351042","webPropertyId":"UA-5351042-34","name":"bmjopen.bmj.com - Reporting","websiteUrl":"http://bmjopen.bmj.com"}],"bmjsiteData":[{"pagePath":"/content/8/3/e019438","pageTitle":"Effect of pasta in the context of low-glycaemic index dietary patterns on body weight and markers of adiposity: a systematic review and meta-analysis of randomised controlled trials in adults | BMJ Open","pageviews":"2153"},{"pagePath":"/content/4/9/e005055","pageTitle":"The role of hospital managers in quality and patient safety: a systematic review | BMJ Open","pageviews":"1016"},{"pagePath":"/content/5/11/e008052.full","pageTitle":"Bicycling injury hospitalisation rates in Canadian jurisdictions: analyses examining associations with helmet legislation and mode share | BMJ Open","pageviews":"686"},{"pagePath":"/content/5/9/e007118","pageTitle":"The effect of statins on average survival in randomised trials, an analysis of end point postponement | BMJ Open","pageviews":"623"},{"pagePath":"/content/6/6/e010401","pageTitle":"Lack of an association or an inverse association between low-density-lipoprotein cholesterol and mortality in the elderly: a systematic review | BMJ Open","pageviews":"502"},{"pagePath":"/content/7/5/e015233","pageTitle":"Long-term antibiotics for prevention of recurrent urinary tract infection in older adults: systematic review and meta-analysis of randomised trials | BMJ Open","pageviews":"495"},{"pagePath":"/content/8/3/e021312.full","pageTitle":"Trends, geographical variation and factors associated with prescribing of gluten-free foods in English primary care: a cross-sectional study | BMJ Open","pageviews":"482"},{"pagePath":"/content/1/2/e000154","pageTitle":"Condom use during commercial sex among clients of Hijra sex workers in Karachi, Pakistan (cross-sectional study) | BMJ Open","pageviews":"385"},{"pagePath":"/content/7/5/e013778","pageTitle":"Reducing social isolation and loneliness in older people: a systematic review protocol | BMJ Open","pageviews":"373"},{"pagePath":"/content/8/4/e021076","pageTitle":"Behaviours preceding suicides at railway and underground locations: a multimethodological qualitative approach | BMJ Open","pageviews":"373"}]}}]},"allWordpressCategory":{"edges":[{"node":{"id":"07acb10c-8a0f-5c7b-8c16-ef2e2c5fd519","name":"About the journal","slug":"about-the-journal"}},{"node":{"id":"dc49b913-f070-5a12-99c2-65b2799a519f","name":"Author Profile","slug":"author-profile"}},{"node":{"id":"69390d22-95b7-5e16-9983-1300b1b77c1c","name":"BMJ Open Editions","slug":"bmj-open-editions"}},{"node":{"id":"4b225b5d-57f6-5fac-881d-8b39604edd1f","name":"Content","slug":"content"}},{"node":{"id":"b9d4de2a-7eda-5a44-9d0e-5348fe6892f3","name":"Discounts","slug":"discounts"}},{"node":{"id":"81455f1d-c264-5716-b8c6-0b6332960946","name":"Editors picks","slug":"editors-picks"}},{"node":{"id":"5e974932-e700-54c2-a58f-b9402ea0f9f4","name":"Events","slug":"events"}},{"node":{"id":"6d9e25d8-8c99-505c-af39-bdc0acbdcc83","name":"Highly accessed","slug":"highly-accessed"}},{"node":{"id":"c0ef9851-2bf8-52ef-b34a-08873ab7b2e7","name":"In the news","slug":"in-the-news"}},{"node":{"id":"1d6a483c-8ecf-5d7d-aff7-83b354811279","name":"Instructions for authors","slug":"instructions-for-authors"}},{"node":{"id":"63c422c3-558c-5df7-8204-be0e85131f8c","name":"Journalology","slug":"journalology"}},{"node":{"id":"fb98bbf0-fa36-5ca8-80ef-4ebc6899a094","name":"Launch","slug":"launch"}},{"node":{"id":"17121eaa-ad8a-5a8e-a3a8-cf47ce39225e","name":"new post","slug":"new-post"}},{"node":{"id":"93d70e43-a898-5f17-98af-3cb280997e3c","name":"Open access","slug":"open-access"}},{"node":{"id":"73eb17de-115a-54c2-9859-6782b36cb1d0","name":"Open peer review","slug":"open-peer-review"}},{"node":{"id":"f24b5a9e-ed37-56c1-978a-9c95dbc988a9","name":"Uncategorized","slug":"uncategorized"}}]}},"pathContext":{"group":[{"node":{"id":"c2b41132-12e8-5bd7-a854-fce0efb225fd","slug":"most-popular-articles-in-july","title":"Most popular articles in July","excerpt":"<p>  The list of the top 10 most-read articles in BMJ Open in July includes some recent content and some older papers. It&#8217;s good to see a range of topics covered, and two study protocols included also. Engkilde et al.&#8217;s recent paper on the association between cancer and contact allergy received considerable coverage in the press [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/08/10/most-popular-articles-in-july/\">Read More&#8230;</a></p>\n","status":"publish","date":"August 10, 2011","template":"","format":"standard"}},{"node":{"id":"fdeb8663-efac-58f7-910d-2cb589e22943","slug":"bmj-open-aiding-data-discovery","title":"BMJ Open aiding data discovery","excerpt":"<p>  We have now published two articles with supplementary data accompanying the article deposited with the Dryad repository: Heneghan et al. on medical device recalls with data here, and Dmello et al. on cystic fibrosis with data here. Authors can share supplementary data or underlying datasets through Dryad. BMJ Open is the first medical journal [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/07/29/bmj-open-aiding-data-discovery/\">Read More&#8230;</a></p>\n","status":"publish","date":"July 29, 2011","template":"","format":"standard"}},{"node":{"id":"7dc77a0b-ea85-5473-9861-ab7304cec175","slug":"203","title":"UK universities minister helps UK research councils affirm OA commitments","excerpt":"<p>  The Higher Education Funding Council (HEFCE) and Research Councils UK (RCUK) &#8211; which includes the Medical Research Council &#8211; recently put out a joint statement reinforcing their open access commitments (here). This announcement came in a speech by David Willetts, Minister for Universities and Science, showing the extent to which the principles of open access are [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/06/06/203/\">Read More&#8230;</a></p>\n","status":"publish","date":"June 06, 2011","template":"","format":"standard"}},{"node":{"id":"6d962517-fc0e-5480-b9e4-14a79eb120a0","slug":"data-available-for-bmj-open-article-on-medical-device-recalls-in-the-uk","title":"Data available for BMJ Open article on medical device recalls","excerpt":"<p>Great news! The full data set for the recently published article, ‘Medical-device recalls in the UK and the device-regulation process: retrospective review of safety notices and alerts’ has been deposited by the authors with DryadUK. The data files can be accessed online here: dx.doi.org./10.5061/dryad.585t4 Data set should be cited as: doi:10.5061/dryad.585t4 [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/05/25/data-available-for-bmj-open-article-on-medical-device-recalls-in-the-uk/\">Read More&#8230;</a></p>\n","status":"publish","date":"May 25, 2011","template":"","format":"standard"}},{"node":{"id":"5b2363ef-2c6b-55a9-bce1-1a383202ab20","slug":"medical-device-recalls-paper-prompts-concern-in-parliament-and-further-coverage","title":"Medical device recalls paper prompts concern in parliament and further coverage","excerpt":"<p>The BMJ Open and BMJ articles (see previous post) and the Channel 4 Dispatches episode on the regulation and recall of medical devices has been covered in various newspapers and even prompted a question in Parliament. MPs Paul Flynn and Peter Bottomley tabled Early Day Motion 1819 noting the findings of the research and calling [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/05/18/medical-device-recalls-paper-prompts-concern-in-parliament-and-further-coverage/\">Read More&#8230;</a></p>\n","status":"publish","date":"May 18, 2011","template":"","format":"standard"}},{"node":{"id":"e53dffb6-af8f-5b27-b421-3dbe20980102","slug":"bmj-open-and-bmj-articles-on-device-recalls","title":"BMJ Open and BMJ articles on device recalls","excerpt":"<p>Yesterday BMJ Open published &#8216;Medical-device recalls in the UK and the device-regulation process: retrospective review of safety notices and alerts&#8216;. This paper sits with features, analysis and commentary in the BMJ and the latest edition of Dispatches, ‘The truth about going under the knife’, on Channel 4 (tonight, 16 May, 8 pm). The full list [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/05/16/bmj-open-and-bmj-articles-on-device-recalls/\">Read More&#8230;</a></p>\n","status":"publish","date":"May 16, 2011","template":"","format":"standard"}},{"node":{"id":"e775a73f-8807-5375-b5ce-aaa06a663538","slug":"open-roads-and-closed-sessions","title":"Open roads and closed sessions","excerpt":"<p>  A recent report on the next steps for increasing open access to UK research concluded that Green OA infrastructure (i.e. repositories) should be encouraged while an economically sustainable transition to Gold OA is worked through.  ‘Heading for the open road: costs and benefits of transitions in scholarly communications’ by CEPA and Mark Ware Consulting, [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/05/10/open-roads-and-closed-sessions/\">Read More&#8230;</a></p>\n","status":"publish","date":"May 10, 2011","template":"","format":"standard"}},{"node":{"id":"75d341de-770d-5d61-99c9-af6c01b7b837","slug":"locked-in-syndrome-article-coverage","title":"Locked-in syndrome article coverage","excerpt":"<p>A month after launch, some of our first papers are still being picked up in the media (see today&#8217;s Irish Medical Times for coverage of our paper on delays in recording diagnoses of ovarian cancer). The paper by Bruno et al. on self-assessed well-being of locked-in syndrome patients received considerable coverage, including this interview on BBC [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/03/25/locked-in-syndrome-article-coverage/\">Read More&#8230;</a></p>\n","status":"publish","date":"March 25, 2011","template":"","format":"standard"}},{"node":{"id":"7ad57d0f-ebb0-503f-9eab-0cac0b1eb0c1","slug":"bmj-open-and-uk-pmc","title":"BMJ Open and UK PMC","excerpt":"<p>Confirmation that BMJ Open satisfies the open access criteria for the Wellcome Trust and other UK PMC funders (including Arthritis Research UK, British Heart Foundation, Cancer Research UK, NIHR and MRC) can be found here, at the UK PMC blog.  BMJ Open will automatically deposit published articles with PMC on authors&#8217; behalf without embargo. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/03/18/bmj-open-and-uk-pmc/\">Read More&#8230;</a></p>\n","status":"publish","date":"March 18, 2011","template":"","format":"standard"}},{"node":{"id":"93c89565-42c8-5c83-9d1d-17c22064c8ea","slug":"bmj-open-publishes-first-papers-coverage-on-bbcs-today-washington-post","title":"BMJ Open publishes first papers; coverage on BBC&#8217;s Today, Washington Post &#8230;","excerpt":"<p>BMJ Open has published its first papers. You can visit the site here. If you are looking for the paper by Bruno et al. on locked-in syndrome, as covered by BBC Radio 4&#8217;s Today programme, the BBC News website, the Washington Post, Times of India, etc., you can find it here. [&#8230;]</p>\n<p><a class=\"btn btn-secondary understrap-read-more-link\" href=\"http://blogs.bmj.com/bmjopen/2011/02/24/bmj-open-publishes-first-papers-coverage-on-bbcs-today-washington-post/\">Read More&#8230;</a></p>\n","status":"publish","date":"February 24, 2011","template":"","format":"standard"}}],"pathPrefix":"","first":false,"last":false,"index":25,"pageCount":27,"additionalContext":{}}}

